.filter {
    width: 155px;
    position: relative;
    &__input {
        position: relative;
        z-index: 2;
        -webkit-appearance: none;
        background: none;
        height: 3px;
        width: 100%;
        outline: none;
        border-radius: 2px;
        &[type="range"]::-webkit-slider-thumb { // for Google
            -webkit-appearance: none;
            appearance: none;
            outline: none;
            cursor: pointer;
            border-radius: 50%;
            background-color: rgba(204, 204, 204, 1);
            width: 14px;
            height: 14px;
        }
        &[type="range"]::-moz-range-thumb {  // for FireFox
            cursor: pointer;
            outline: none;
            border-radius: 50%;
            background-color: rgba(204, 204, 204, 1);
            width: 14px;
            height: 14px;
        }
    }
    &__progress {
        position: absolute;
        z-index: 1;
        left: 0;
        top: 11px;
        width: 100%;
        height: 3px;
        background-color: #ccc;
        border-radius: 2px;
        overflow: hidden;
    }
    &__progress-fill {
        display: block;
        width: 0;
        height: 100%;
        background-color: #6ebbd3;;
    }
    &__indicators {
        width: 100%;
        font-size: 15px;
        display: flex;
        justify-content: space-between;
    }
    &__counter {
        padding: 10px 0 0 0;
        left: 0;
    }
    &__current {
        background-color: #777990;
        color: #fff;
        font-size: 12px;
        padding: 5px;
        border-radius: 5px;
        position: absolute;
        top: -60%;
        left: 0;
    }
}
